.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  width: 90%;
  margin: 0 auto;
  background-color: aliceblue;
}

header {
  /* flex-basis: 30vh; */
  height: 30vh;
  background-color: burlywood;
  overflow: hidden;
}

main {
  display: flex;
  flex-grow: 1;
  padding: 20px;
}

aside {
  min-width: 200px;
  background-color: rgb(252, 221, 82)
}

.page {
  background-color: aquamarine;
  flex-grow: 1;
}

footer {
  background-color: coral;
  line-height: 50px;
  text-align: center;
}
img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

nav ul {
  list-style: none;
}
nav li {
  margin: 5px 0;
}

nav a {
  display: block;
  padding: 5px 0px 5px 10px;
  text-decoration: none;
  text-transform: uppercase;
  color: #666;
  /* border-left: 5px transparent solid; */
  transition: 0.2s
}

nav a:hover {
  -webkit-transform: translateX(5px);
          transform: translateX(5px);
}
nav a.start-active {
  border-left: 5px red solid;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
nav a.products-active {
  border-left: 5px rgb(179, 0, 179) solid;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
nav a.contact-active {
  border-left: 5px rgb(67, 211, 91) solid;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
nav a.admin-active {
  border-left: 5px rgb(255, 81, 168) solid;
  -webkit-transform: translateX(0);
          transform: translateX(0);
}
article {
  padding: 20px 20px 0px 20px;
}

.title, .author, .text {
  padding: 5px;
}

.products-list h2{
  padding: 20px;
}

.products-list ul {
  list-style: none;
  padding-left: 20px;
}

.products-list a {
  display: block;
  padding: 2px;
  margin: 5px;
  text-decoration: none;
  color: #555
}
.product h1, p , h3{
  padding: 5px;
  margin-bottom: 10px;
}

form {
  width: 400px;
  margin: 0 auto;
  padding: 10px;
  text-align: right;
}

form textarea {
  width: 100%
}
form span {
  margin-right: 20px;
  color: red;
}

form button {
  width: 150px;
  padding: 5px;
}
label {
  display: block;
  padding: 20px
}

button {
  padding: 5px 20px;
  margin-left: 20px;
}


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: Raleway, sans-serif;
}

